
import * as React from "react";
import { useMutation, useQuery, useQueryClient } from "@tanstack/react-query";

const previewModeQuery = "preview-mode-query" as const;
type TPreviewModeQuery = [boolean, () => void, () => void];

/**
 * Custom hook to manage preview mode state.
 *
 * @returns {TPreviewModeQuery} A tuple containing:
 * - `isPreviewMode` (boolean): The current state of preview mode.
 * - `enterPreviewMode` (function): Function to enable preview mode.
 * - `exitPreviewMode` (function): Function to disable preview mode.
 */
export function usePreviewMode(): TPreviewModeQuery {
  const queryClient = useQueryClient();

  const { data: isPreviewMode } = useQuery(
    {
      queryKey: [previewModeQuery],
      queryFn: async () => {
        return false;
      },
      initialData: false,
    },
    queryClient
  );
  const enterPreviewMode = () => {
    queryClient.setQueryData([previewModeQuery], true);
  };
  const exitPreviewMode = () => {
    queryClient.setQueryData([previewModeQuery], false);
  };

  return [isPreviewMode, enterPreviewMode, exitPreviewMode];
}
